import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
import remarkMath from 'remark-math';
import rehypeKatex from 'rehype-katex';
import 'katex/dist/katex.min.css';

// 全局API密钥存储
export function getGlobalApiKey() {
  return localStorage.getItem('global_api_key') || '';
}
export function setGlobalApiKey(key) {
  localStorage.setItem('global_api_key', key || '');
}

const ApiConfigurator = ({currentItem, currentType }) => {
  const [apiKey, setApiKey] = useState(getGlobalApiKey());
  const [response, setResponse] = useState('');
  const [customPromptMode, setCustomPromptMode] = useState(false);
  const [apiType, setApiType] = useState('default'); // default/siliconflow 或 deepseek
  const [showApiKeyModal, setShowApiKeyModal] = useState(false);
  const [siliconflowKey, setSiliconflowKey] = useState(localStorage.getItem('global_api_key_siliconflow') || '');
  const [deepseekKey, setDeepseekKey] = useState(localStorage.getItem('global_api_key_deepseek') || '');

  const generateQueryFromTemplate = () => {
    if (!currentItem) {
      return '';
    }

    if (currentType === '概念') {
      return `请用中文解释以下概念或例子，注意解释要简练，并且结合数学公式，给出直观的解释，和一个具体的例子：\n\n名称：${currentItem.概念名}\n定义：${currentItem.概念定义}`;
    } else if (currentType === '定理') {
      return `请用中文解释以下定理和证明，解释需要指出这个定理的证明思路的出发点是什么，以及整个思路里面关键环节，用自然语言解释每个环节，给出这些自然语言对应的数学公式，注意解释要简练：\n\n内容：${currentItem.定理内容}\n证明：${currentItem.证明}`;
    }

    return '';
  };

  const [query, setQuery] = useState(generateQueryFromTemplate());

  useEffect(() => {
    const updatedQuery = generateQueryFromTemplate();
    setQuery(updatedQuery);
  }, [currentItem, currentType]);

  const handleApiKeyChange = (e) => {
    setApiKey(e.target.value);
    setGlobalApiKey(e.target.value);
  };

  const handleCustomPromptToggle = () => {
    setCustomPromptMode(v => !v);
    if (!customPromptMode) {
      // 切换到自定义时，保留当前 query
    } else {
      // 切回自动模式时，恢复自动生成内容
      setQuery(generateQueryFromTemplate());
    }
  };

  const handleSaveApiKeys = () => {
    localStorage.setItem('global_api_key_siliconflow', siliconflowKey || '');
    localStorage.setItem('global_api_key_deepseek', deepseekKey || '');
    setShowApiKeyModal(false);
  };

  const getCurrentApiKey = () => {
    return apiType === 'deepseek' ? deepseekKey : siliconflowKey;
  };

  const handleSendQuery = async () => {
    const usedKey = getCurrentApiKey();
    const usedQuery = customPromptMode ? query : generateQueryFromTemplate();
    setQuery(usedQuery);
    if (!usedKey || !usedQuery) {
      alert('请填写API密钥和问题');
      return;
    }
    let options, url;
    if (apiType === 'deepseek') {
      url = 'https://api.deepseek.com/v1/chat/completions';
      options = {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${usedKey}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          model: 'deepseek-chat',
          messages: [
            { role: 'user', content: usedQuery },
          ],
          temperature: 0.7,
        }),
      };
    } else {
      url = 'https://api.siliconflow.cn/v1/chat/completions';
      options = {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${usedKey}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          model: 'Qwen/Qwen3-8B',
          messages: [
            { role: 'user', content: usedQuery },
          ],
          stream: false,
          max_tokens: 4096,
          enable_thinking: false,
          thinking_budget: 4096,
          min_p: 0.05,
          stop: null,
          temperature: 0.7,
          top_p: 0.7,
          top_k: 50,
          frequency_penalty: 0.5,
          n: 1,
          response_format: { type: 'text' },
          tools: [
            {
              type: 'function',
              function: {
                description: '<string>',
                name: '<string>',
                parameters: {},
                strict: false,
              },
            },
          ],
        }),
      };
    }
    fetch(url, options)
      .then((response) => response.json())
      .then((data) => {
        setResponse(data.choices?.[0]?.message?.content || '');
      })
      .catch((err) => {
        console.error('Error fetching API:', err);
        alert('请求失败，请检查API密钥或网络连接');
      });
  };

  return (
    <div style={{ display: 'flex', alignItems: 'flex-start', gap: 36, background: '#f6faff', borderRadius: 18, boxShadow: '0 4px 32px 0 rgba(59,130,246,0.08)', padding: 32, margin: '0 auto', maxWidth: 1100 }}>
      {/* 左侧API配置卡片 */}
      <div style={{
        border: '1.5px solid #dbeafe',
        borderRadius: 16,
        background: '#fff',
        padding: 28,
        width: 260,
        minWidth: 200,
        maxWidth: 200,
        boxShadow: '0 2px 12px #2563eb11',
        marginTop: 8,
        display: 'flex',
        flexDirection: 'column',
        gap: 18,
      }}>
        <h2 style={{ fontSize: 19, fontWeight: 700, marginBottom: 8, color: '#2563eb', letterSpacing: 1 }}>大模型 API 配置</h2>
        <div>
          {/* 顶部并排：API类型选择 + 配置密钥按钮 */}
          <div style={{ display: 'flex', alignItems: 'center', gap: 10, marginBottom: 8 }}>
            <select value={apiType} onChange={e => setApiType(e.target.value)} style={{padding:'4px 12px',borderRadius:8,border:'1.5px solid #2563eb',fontWeight:600,fontSize:14,cursor:'pointer',boxShadow:'0 1px 4px #2563eb11',background:'#f8fafc',color:'#2563eb'}}>
              <option value="default">SiliconFlow</option>
              <option value="deepseek">DeepSeek</option>
            </select>
            <button
              onClick={() => setShowApiKeyModal(true)}
              style={{padding:'4px 12px',borderRadius:8,border:'1.5px solid #2563eb',background:'#f8fafc',color:'#2563eb',fontWeight:600,fontSize:14,cursor:'pointer',boxShadow:'0 1px 4px #2563eb11'}}
            >
              配置
            </button>
          </div>
          {showApiKeyModal && (
            <div style={{position:'fixed',top:0,left:0,width:'100vw',height:'100vh',background:'rgba(0,0,0,0.18)',zIndex:3000,display:'flex',alignItems:'center',justifyContent:'center'}}>
              <div style={{background:'#fff',borderRadius:16,boxShadow:'0 4px 32px #2563eb22',padding:36,minWidth:320,maxWidth:400,width:'100%',position:'relative',display:'flex',flexDirection:'column',gap:18}}>
                <button style={{position:'absolute',top:12,right:18,background:'none',border:'none',color:'#2563eb',fontSize:22,fontWeight:700,cursor:'pointer'}} onClick={()=>setShowApiKeyModal(false)} title="关闭">×</button>
                <h3 style={{marginTop:0,marginBottom:18,fontWeight:700,fontSize:20,color:'#2563eb'}}>API密钥配置</h3>
                <div>
                  <label style={{fontWeight:600,fontSize:15}}>SiliconFlow API Key：</label>
                  <input type="password" value={siliconflowKey} onChange={e=>setSiliconflowKey(e.target.value)} style={{marginLeft:8,padding:'4px 12px',borderRadius:6,border:'1.2px solid #b6c2d1',width:260}} placeholder="粘贴你的SiliconFlow密钥" />
                </div>
                <div>
                  <label style={{fontWeight:600,fontSize:15}}>DeepSeek API Key：</label>
                  <input type="password" value={deepseekKey} onChange={e=>setDeepseekKey(e.target.value)} style={{marginLeft:8,padding:'4px 12px',borderRadius:6,border:'1.2px solid #b6c2d1',width:260}} placeholder="粘贴你的DeepSeek密钥" />
                </div>
                <button style={{background:'#2563eb',color:'#fff',border:'none',borderRadius:8,padding:'8px 24px',fontWeight:700,fontSize:16,cursor:'pointer',marginTop:8,boxShadow:'0 2px 8px #2563eb22'}} onClick={handleSaveApiKeys}>保存密钥</button>
              </div>
            </div>
          )}
        </div>
        {/* 问题区和自定义Prompt切换分组，label和按钮并排，按钮在右侧 */}
        <div>
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 4 }}>
            <label style={{ fontSize: 15, fontWeight: 500, color: '#374151', marginLeft: 2 }}>问题</label>
            <button
              onClick={handleCustomPromptToggle}
              style={{padding:'2px 10px',borderRadius:8,border:'1.5px solid #2563eb',background:customPromptMode?'#2563eb':'#f8fafc',color:customPromptMode?'#fff':'#2563eb',fontWeight:600,fontSize:13,cursor:'pointer',boxShadow:'0 1px 4px #2563eb11'}}
              title={customPromptMode ? '返回自动生成' : '切换到自定义Prompt'}
            >
              {customPromptMode ? '返回自动生成' : '自定义Prompt'}
            </button>
          </div>
          <textarea
            value={query}
            onChange={e => setQuery(e.target.value)}
            rows={5}
            style={{width:'100%',border:'1px solid #b6c2d1',borderRadius:6,padding:8,fontSize:14,marginTop:2,resize:'vertical'}}
            disabled={!customPromptMode && !currentItem}
            placeholder={customPromptMode ? '请输入自定义Prompt' : '问题将根据当前显示的内容自动生成'}
          />
        </div>
        {/* 发送问题按钮单独一行 */}
        <button
          onClick={handleSendQuery}
          style={{display:'inline-block',width:'100%',padding:'12px 0',fontSize:'16px',fontWeight:700,textAlign:'center',color:'#fff',background:'linear-gradient(90deg,#60a5fa 0%,#2563eb 100%)',border:'none',borderRadius:10,boxShadow:'0 2px 8px #2563eb22',cursor:'pointer',transition:'background 0.2s',marginTop:8,letterSpacing:1}}
        >
          发送问题
        </button>
      </div>
      {/* 右侧模型返回内容 */}
      <div style={{ flex: 1, minWidth: 0 }}>
        {response && (
          <div style={{ marginTop: 0, padding: 32, border: '1.5px solid #dbeafe', borderRadius: 16, background: '#f8fafc', minHeight: 120, boxShadow: '0 2px 12px #2563eb11' }}>
            <h3 style={{ fontSize: 16, fontWeight: 700, marginBottom: 12, color: '#2563eb' }}>模型返回的内容:</h3>
            <ReactMarkdown
              remarkPlugins={[remarkMath]}
              rehypePlugins={[rehypeKatex]}
              components={{
                p: ({ node, ...props }) => <p {...props} style={{ fontSize: 16, color: '#374151', whiteSpace: 'pre-wrap', margin: 0 }} />,
              }}
            >
              {response}
            </ReactMarkdown>
          </div>
        )}
      </div>
    </div>
  );
};

export default ApiConfigurator;