import React, { useState, useEffect, useRef } from 'react';
import ReactMarkdown from 'react-markdown';
import remarkMath from 'remark-math';
import rehypeKatex from 'rehype-katex';
import remarkGfm from 'remark-gfm';
import rehypeHighlight from 'rehype-highlight';
import './BookReader.css';
import JsonAnote from './JsonAnote';

// BookReader 组件：支持上传 JSON 文件，解析后展示书籍章节、段落、总结
function BookReader({ bookData: propBookData, hideMenu, hideReload, sidebarOpen, setFolderTree, buildFolderTree, filePath }) {
  // 新增：支持直接传入 bookData
  const [bookData, setBookData] = useState(propBookData || []);
  // 当前选中的章节索引
  const [chapterIdx, setChapterIdx] = useState(0);
  // 控制目录弹窗显示
  const [showBookMenu, setShowBookMenu] = useState(false);
  // books 仅菜单模式下需要，去除 setBooks 警告
  const [books] = useState([]);
  const [showAnote, setShowAnote] = useState(false);
  const [anoteConfig, setAnoteConfig] = useState(null);
  const [anoteLoading, setAnoteLoading] = useState(false);
  const [anoteProgress, setAnoteProgress] = useState({ current: 0, total: 0, status: '' });
  const anoteStopRef = useRef(false);

  // 兼容：如果 propBookData 变化，自动更新
  useEffect(() => {
    if (propBookData) {
      setBookData(propBookData);
      setChapterIdx(0);
    }
  }, [propBookData]);

  // 添加键盘事件监听的 useEffect
  useEffect(() => {
    const handleKeyDown = (e) => {
      // 原有 Ctrl+Q 逻辑保持不变
      const isCtrl = e.ctrlKey || e.metaKey;
      if (isCtrl && e.key.toLowerCase() === 'q') {
        e.preventDefault();
        if (!anoteLoading && !showAnote) {
          setShowAnote(true);
        }
      }

      // 新增：Ctrl+E 回到顶端逻辑
      if (isCtrl && e.key === '`') {
        e.preventDefault(); // 阻止默认行为
        // 滚动到 BookReader 容器顶端
        window.scrollTo({ top: 0, behavior: 'smooth' }); // 平滑滚动到顶部
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [anoteLoading, showAnote]); // 依赖不变

  // 处理文件上传
  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const json = JSON.parse(event.target.result);
        setBookData(json);
        setChapterIdx(0);
      } catch (err) {
        alert('文件内容不是有效的JSON格式');
      }
    };
    reader.readAsText(file);
  };

  // 选择public目录下的书，弹窗目录
  const handleSelectPublicBook = () => {
    setShowBookMenu(true);
  };

  // 仅菜单模式下才需要 handleChooseBook
  const handleChooseBook = async (idx) => {
    setShowBookMenu(false);
    if (idx >= 0 && idx < books.length) {
      try {
        const res = await fetch((process.env.PUBLIC_URL || '') + books[idx].file);
        const json = await res.json();
        setBookData(json);
        setChapterIdx(0);
      } catch (err) {
        alert('加载失败，请检查 public 目录下是否有对应文件');
      }
    }
  };

  // 章节列表
  const chapters = Array.isArray(bookData) ? bookData : [];
  const currentChapter = chapters[chapterIdx] || {};
  const paragraphs = Array.isArray(currentChapter.内容) ? currentChapter.内容 : [];

  // 动态maxWidth：侧栏打开时 900，否则 1200
  const maxWidth = sidebarOpen ? 950 : 1200;

  // 自动保存 bookData 到本地文件和虚拟文件系统
  const autoSaveBookData = async (data) => {
    try {
      const finalSavePath = filePath || (window.fs && window.fs.currentFilePath ? window.fs.currentFilePath : '/book_ai_annotated.json');
      // 本地应用：保存到真实本地文件
      if (window.electronAPI && finalSavePath && window.__md_root_dir) {
        const absPath = window.__md_root_dir.replace(/\\$/, '') + finalSavePath;
        await window.electronAPI.saveFile(absPath, JSON.stringify(data, null, 2));
      }
      // 虚拟文件系统（内存刷新失效）
      if (finalSavePath && window.__md_file_map) {
        const file = new File([JSON.stringify(data, null, 2)], finalSavePath.split('/').pop(), { type: 'application/json' });
        file.__relativePath = finalSavePath.replace(/\\/g, '/').replace(/^\//, '');
        window.__md_file_map[finalSavePath] = file;
        if (typeof setFolderTree === 'function' && typeof buildFolderTree === 'function') {
          const files = Object.values(window.__md_file_map);
          setFolderTree(buildFolderTree(files));
        }
      }
    } catch {}
  };

  // AI注释主逻辑
  // 停止AI注释
  function handleStopAnote() {
    anoteStopRef.current = true;
    setAnoteLoading(false);
    setAnoteProgress(p => ({ ...p, status: '已中断' }));
  }

  async function handleStartAnote(config) {
    setAnoteConfig(config);
    setAnoteLoading(true);
    anoteStopRef.current = false;
    let data = JSON.parse(JSON.stringify(bookData));
    let total = 0, count = 0;
    if (!Array.isArray(data)) return;
    total = data.reduce((sum, sec) => sum + (Array.isArray(sec.内容) ? sec.内容.length : 0), 0);
    setAnoteProgress({ current: 0, total, status: '处理中...' });
    for (let i = 0; i < data.length; i++) {
      const section = data[i];
      if (!Array.isArray(section.内容)) continue;
      for (let j = 0; j < section.内容.length; j++) {
        if (anoteStopRef.current) {
          setAnoteLoading(false);
          setAnoteProgress(p => ({ ...p, status: '已中断' }));
          return;
        }
        const para = section.内容[j];
        // 只注释当前栏（para._summaryIndex），多栏时只写入对应栏
        const summaryIdx = typeof para._summaryIndex === 'number' ? para._summaryIndex : 0;
        let needAnote = false;
        if (Array.isArray(para.总结)) {
          needAnote = !para.总结[summaryIdx] && para.段落 && para.段落.length >= (config.minLength || 30);
        } else {
          needAnote = !para.总结 && para.段落 && para.段落.length >= (config.minLength || 30);
        }
        if (needAnote) {
          // 请求AI
          try {
            const prompt = config.prompt || `请用中文简要指出这个小节提出的概念、算法、理论、定理或命题、数学推导，如果这些东西在文中有具体内容，都在返回的内容里给出。开头不要说多余的话。返回中文，然后每一部分，直接返回"- "开头的格式，注意每个部分不能省略关键信息，不要在每一部分前加上"**某某**"，直接返回内容即可。还有，你必须注意可读性，这个文本是放在正文的侧栏。一个读者希望能读你返回的内容，清楚简洁，提纲挈领，读完回来看会很清楚。注意，有数学公式的文本，你返回的内容要包含必要的数学公式：\n`;
            let fetchOptions;
            let url = config.apiUrl;
            if (config.apiType === 'deepseek') {
              fetchOptions = {
                method: 'POST',
                headers: {
                  'Authorization': `Bearer ${config.token}`,
                  'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                  model: config.model,
                  messages: [
                    { role: 'user', content: prompt + para.段落 }
                  ],
                  temperature: config.temperature,
                })
              };
            } else {
              fetchOptions = {
                method: 'POST',
                headers: {
                  'Authorization': `Bearer ${config.token}`,
                  'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                  model: config.model,
                  messages: [
                    { role: 'user', content: prompt + para.段落 }
                  ],
                  stream: false,
                  max_tokens: config.maxTokens,
                  enable_thinking: false,
                  thinking_budget: config.thinkingBudget,
                  min_p: config.minP,
                  stop: [],
                  temperature: config.temperature,
                  top_p: config.topP,
                  top_k: config.topK,
                  frequency_penalty: config.frequencyPenalty,
                  n: 1,
                  response_format: { type: 'text' },
                  tools: [
                    {
                      type: 'function',
                      function: {
                        description: '',
                        name: '',
                        parameters: {},
                        strict: false
                      }
                    }
                  ]
                })
              };
            }
            const res = await fetch(url, fetchOptions);
            const result = await res.json();
            const aiContent = (result.choices && result.choices[0] && result.choices[0].message && result.choices[0].message.content) ? result.choices[0].message.content.trim() : '总结失败';
            if (Array.isArray(para.总结)) {
              para.总结[summaryIdx] = aiContent;
            } else if (summaryIdx === 0) {
              para.总结 = aiContent;
            }
          } catch {
            if (Array.isArray(para.总结)) {
              para.总结[summaryIdx] = '总结失败';
            } else if (summaryIdx === 0) {
              para.总结 = '总结失败';
            }
          }
          // 实时保存到虚拟文件系统（window.__md_file_map）并刷新文件树
          try {
            const finalSavePath = filePath || (window.fs && window.fs.currentFilePath ? window.fs.currentFilePath : '/book_ai_annotated.json');
            if (window.electronAPI && finalSavePath && window.__md_root_dir) {
              const absPath = window.__md_root_dir.replace(/\\$/, '') + finalSavePath;
              await window.electronAPI.saveFile(absPath, JSON.stringify(data, null, 2));
            }
            if (finalSavePath && window.__md_file_map) {
              const file = new File([JSON.stringify(data, null, 2)], finalSavePath.split('/').pop(), { type: 'application/json' });
              file.__relativePath = finalSavePath.replace(/\\/g, '/').replace(/^\//, '');
              window.__md_file_map[finalSavePath] = file;
              if (typeof setFolderTree === 'function' && typeof buildFolderTree === 'function') {
                const files = Object.values(window.__md_file_map);
                setFolderTree(buildFolderTree(files));
              }
              setBookData(JSON.parse(JSON.stringify(data)));
            }
          } catch {}
          await new Promise(r => setTimeout(r, 600));
        }
        count++;
        setAnoteProgress({ current: count, total, status: `已处理 ${count}/${total} 段` });
      }
    }
    setAnoteLoading(false);
    setAnoteProgress({ current: total, total, status: '全部完成' });
  }

  // 渲染部分，隐藏菜单和重新加载按钮
  return (
    <div style={{ width: '100%', maxWidth, margin: '0 auto', padding: 24, boxSizing: 'border-box', overflowX: 'auto' }}>
      {/* 上传区 */}
      {!bookData && !hideMenu && (
        <div style={{ margin: '40px 0', textAlign: 'center' }}>
          <input
            type="file"
            accept=".json"
            onChange={handleFileUpload}
            style={{ display: 'none' }}
            id="book-upload"
          />
          <label htmlFor="book-upload" style={{
            display: 'inline-block',
            padding: '16px 36px',
            background: '#2563eb',
            color: '#fff',
            borderRadius: 10,
            fontSize: 18,
            fontWeight: 600,
            cursor: 'pointer',
            boxShadow: '0 2px 8px #2563eb22',
            marginBottom: 18
          }}>
            上传书籍JSON文件
          </label>
          <button
            onClick={handleSelectPublicBook}
            style={{
              display: 'inline-block',
              marginLeft: 18,
              padding: '16px 36px',
              background: '#f3f6fa',
              color: '#2563eb',
              borderRadius: 10,
              fontSize: 18,
              fontWeight: 600,
              cursor: 'pointer',
              boxShadow: '0 2px 8px #2563eb22',
              border: '1.5px solid #2563eb',
              marginBottom: 18
            }}
          >
            选择已有书籍
          </button>
          <div style={{ color: '#888', marginTop: 12 }}>请上传结构为章节-段落-总结的JSON文件，或选择已有书籍</div>
        </div>
      )}
      {/* 选择已有书籍按钮始终存在 */}
      {bookData && (
        <div style={{ marginBottom: 10, textAlign: 'right' }}>
          {/* 选择已有书籍按钮，只有非hideMenu时显示 */}
          {!hideMenu && (
            <button
              onClick={handleSelectPublicBook}
              style={{
                display: 'inline-block',
                padding: '7px 22px',
                background: '#f3f6fa',
                color: '#2563eb',
                borderRadius: 8,
                fontSize: 15,
                fontWeight: 600,
                cursor: 'pointer',
                border: '1px solid #2563eb',
                boxShadow: '0 1px 4px #2563eb11',
                transition: 'all 0.2s',
                marginRight: 8
              }}
            >
              选择已有书籍
            </button>
          )}
          {/* AI生成注释按钮，任何模式都显示 */}
          <button
            onClick={()=>setShowAnote(true)}
            style={{
              display: 'inline-block',
              padding: '7px 22px',
              background: anoteLoading?'#e0e7ef':'#2563eb',
              color: anoteLoading?'#888':'#fff',
              borderRadius: 8,
              fontSize: 15,
              fontWeight: 600,
              cursor: anoteLoading?'not-allowed':'pointer',
              border: '1px solid #2563eb',
              boxShadow: '0 1px 4px #2563eb11',
              transition: 'all 0.2s',
              marginRight: 8
            }}
            disabled={anoteLoading}
          >
            AI生成注释
          </button>
          {/* 停止AI注释按钮 */}
          {anoteLoading && (
            <button
              onClick={handleStopAnote}
              style={{
                display: 'inline-block',
                padding: '7px 22px',
                background: '#f87171',
                color: '#fff',
                borderRadius: 8,
                fontSize: 15,
                fontWeight: 600,
                cursor: 'pointer',
                border: '1px solid #f87171',
                boxShadow: '0 1px 4px #f8717111',
                transition: 'all 0.2s',
                marginRight: 8
              }}
            >
              停止AI注释
            </button>
          )}
          {anoteLoading && (
            <span style={{marginLeft:12,color:'#2563eb',fontWeight:600}}>{anoteProgress.status}</span>
          )}
        </div>
      )}
      {/* 目录弹窗 */}
      {showBookMenu && (
        <div style={{
          position: 'fixed', left: 0, top: 0, width: '100vw', height: '100vh', zIndex: 1000,
          background: 'rgba(0,0,0,0.18)', display: 'flex', alignItems: 'center', justifyContent: 'center'
        }}>
          <div style={{ background: '#fff', borderRadius: 16, boxShadow: '0 4px 32px #2563eb22', padding: 36, minWidth: 320, minHeight: 120 }}>
            <h3 style={{ fontSize: 20, fontWeight: 700, color: '#2563eb', marginBottom: 18 }}>选择一本书</h3>
            <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
              {books.map((b, idx) => (
                <li key={b.file} style={{ marginBottom: 14 }}>
                  <button
                    onClick={() => handleChooseBook(idx)}
                    style={{
                      width: '100%',
                      textAlign: 'left',
                      padding: '12px 18px',
                      borderRadius: 8,
                      border: '1.5px solid #2563eb',
                      background: '#f8fafc',
                      color: '#2563eb',
                      fontWeight: 600,
                      fontSize: 16,
                      cursor: 'pointer',
                      boxShadow: '0 1px 4px #2563eb11',
                      transition: 'all 0.2s',
                    }}
                  >
                    <div style={{ fontSize: 17, fontWeight: 700 }}>{b.name}</div>
                    <div style={{ fontSize: 14, color: '#888', marginTop: 2 }}>{b.desc}</div>
                  </button>
                </li>
              ))}
            </ul>
            <button
              onClick={() => setShowBookMenu(false)}
              style={{ marginTop: 18, color: '#888', background: 'none', border: 'none', fontSize: 15, cursor: 'pointer' }}
            >
              取消
            </button>
          </div>
        </div>
      )}
      {/* 章节导航与内容展示 */}
      {bookData && (
        <>
          {/* 重新加载按钮 */}
          {!hideReload && (
            <div style={{ marginBottom: 18, textAlign: 'right' }}>
              <input
                type="file"
                accept=".json"
                onChange={handleFileUpload}
                style={{ display: 'none' }}
                id="book-reload"
              />
              <label htmlFor="book-reload" style={{
                display: 'inline-block',
                padding: '7px 22px',
                background: '#f3f6fa',
                color: '#2563eb',
                borderRadius: 8,
                fontSize: 15,
                fontWeight: 600,
                cursor: 'pointer',
                border: '1px solid #2563eb',
                boxShadow: '0 1px 4px #2563eb11',
                transition: 'all 0.2s',
              }}>
                重新加载书籍JSON文件
              </label>
            </div>
          )}
          {/* 章节导航 - 横向可滚动 */}
          <div style={{
            display: 'flex',
            gap: 12,
            marginBottom: 24,
            overflowX: 'auto',
            paddingBottom: 6,
            scrollbarWidth: 'thin',
            scrollbarColor: '#2563eb22 #f3f6fa',
            WebkitOverflowScrolling: 'touch',
          }}>
            {chapters.map((ch, idx) => (
              <button
                key={idx}
                onClick={() => setChapterIdx(idx)}
                style={{
                  flex: '0 0 auto',
                  padding: '8px 18px',
                  borderRadius: 8,
                  border: idx === chapterIdx ? '2px solid #2563eb' : '1px solid #d1d5db',
                  background: idx === chapterIdx ? '#2563eb' : '#f3f6fa',
                  color: idx === chapterIdx ? '#fff' : '#222',
                  fontWeight: idx === chapterIdx ? 700 : 500,
                  cursor: 'pointer',
                  boxShadow: idx === chapterIdx ? '0 2px 8px #2563eb22' : 'none',
                  transition: 'all 0.2s',
                  minWidth: 90,
                  marginRight: 2,
                }}
              >
                {ch.标题 || `章节${idx + 1}`}
              </button>
            ))}
          </div>

          {/* 主内容区：正文markdown+总结对齐 */}
          <div style={{ background: '#fff', borderRadius: 16, boxShadow: '0 2px 12px #2563eb11', padding: 28, minWidth: 0 }}>
            <h2 style={{ fontSize: 22, fontWeight: 700, marginBottom: 18 }}>
              {currentChapter.标题 ? currentChapter.标题.replace(/^#+\s*/, '') : ''}
            </h2>
            {paragraphs.map((para, idx) => {
              // 兼容总结栏为字符串或数组
              let summaryArr = Array.isArray(para.总结) ? para.总结 : (para.总结 ? [para.总结] : []);
              // 最多三栏
              while (summaryArr.length > 3) summaryArr = summaryArr.slice(0, 3);
              // 当前选中的栏索引
              if (para._summaryIndex === undefined) para._summaryIndex = 0;
              const setSummaryIndex = (newIdx) => {
                para._summaryIndex = newIdx;
                setBookData([...bookData]);
              };
              const setSummaryArr = (arr) => {
                para.总结 = arr.length === 1 ? arr[0] : arr;
                setBookData([...bookData]);
              };
              return (
                <div key={idx} style={{
                  display: 'flex',
                  alignItems: 'flex-start',
                  gap: 24,
                  marginBottom: 36,
                  borderBottom: '1px solid #e5e7eb',
                  paddingBottom: 18,
                  flexWrap: 'wrap',
                  position: 'relative',
                }}>
                  {/* 正文区域 */}
                  <div style={{
                    flex: 2,
                    minWidth: 0,
                    fontSize: 17,
                    lineHeight: 1.8,
                    marginBottom: 8,
                    wordBreak: 'break-word',
                    position: 'relative',
                  }}>
                    <ReactMarkdown remarkPlugins={[remarkMath, remarkGfm]} rehypePlugins={[rehypeKatex]}>{para.段落 || ''}</ReactMarkdown>
                    {/* 复制按钮 */}
                    <button
                      onClick={() => navigator.clipboard.writeText(para.段落 || '')}
                      title="复制正文"
                      style={{
                        position: 'absolute',
                        top: 6,
                        right: 6,
                        background: '#f3f6fa',
                        border: '1px solid #b6c2d1',
                        borderRadius: '50%',
                        width: 22,
                        height: 22,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: 13,
                        color: '#2563eb',
                        cursor: 'pointer',
                        boxShadow: '0 1px 4px #2563eb11',
                        zIndex: 2,
                        padding: 0,
                      }}
                    >
                      <span style={{fontWeight:700}}>⧉</span>
                    </button>
                  </div>
                  {/* 总结区域 */}
                  <div style={{
                    flex: 1,
                    minWidth: 180,
                    background: '#f8fafc',
                    borderRadius: 8,
                    padding: '12px 16px',
                    color: '#2563eb',
                    fontSize: 15,
                    marginTop: 0,
                    boxShadow: '0 1px 4px #2563eb11',
                    borderLeft: '4px solid #2563eb',
                    fontWeight: 500,
                    minHeight: 28,
                    marginLeft: 0,
                    wordBreak: 'break-word',
                    position: 'relative',
                  }}>
                    {/* 栏切换与操作按钮 */}
                    <div style={{display:'flex',alignItems:'center',gap:6,marginBottom:6}}>
                      {summaryArr.map((_, i) => (
                        <button
                          key={i}
                          onClick={() => setSummaryIndex(i)}
                          style={{
                            padding:'2px 8px',
                            borderRadius:6,
                            border: para._summaryIndex===i?'1.5px solid #2563eb':'1px solid #b6c2d1',
                            background: para._summaryIndex===i?'#2563eb':'#f8fafc',
                            color: para._summaryIndex===i?'#fff':'#2563eb',
                            fontWeight:600,
                            fontSize:13,
                            cursor:'pointer',
                            marginRight:2
                          }}
                        >栏{i+1}</button>
                      ))}
                      {/* 增加栏按钮 */}
                      {summaryArr.length<3 && (
                        <button
                          onClick={async () => {
                            if (!Array.isArray(para.总结)) {
                              para.总结 = para.总结 ? [para.总结] : [];
                            }
                            para.总结.push('');
                            para._summaryIndex = para.总结.length - 1;
                            setBookData([...bookData]);
                            await autoSaveBookData(bookData);
                          }}
                          style={{padding:'2px 8px',borderRadius:6,border:'1px solid #60a5fa',background:'#e0f2fe',color:'#2563eb',fontWeight:600,fontSize:13,cursor:'pointer'}}
                          title="增加总结栏"
                        >＋</button>
                      )}
                      {/* 删除栏按钮（仅限第2/3栏） */}
                      {summaryArr.length>1 && para._summaryIndex>0 && (
                        <button
                          onClick={async () => {
                            const arr = summaryArr.filter((_, i) => i !== para._summaryIndex);
                            setSummaryArr(arr);
                            setSummaryIndex(0);
                            await autoSaveBookData(bookData);
                          }}
                          style={{padding:'2px 8px',borderRadius:6,border:'1px solid #f87171',background:'#fff',color:'#f87171',fontWeight:600,fontSize:13,cursor:'pointer'}}
                          title="删除当前栏"
                        >删</button>
                      )}
                    </div>
                    {/* 当前栏内容显示与删除按钮 */}
                    {summaryArr[para._summaryIndex] && (
                      <button
                        onClick={async () => {
                          summaryArr[para._summaryIndex] = '';
                          setSummaryArr([...summaryArr]);
                          await autoSaveBookData(bookData);
                        }}
                        title="清空本栏内容"
                        style={{
                          position: 'absolute',
                          top: 6,
                          right: 6,
                          background: '#fff',
                          border: '1px solid #f87171',
                          borderRadius: '50%',
                          width: 20,
                          height: 20,
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          fontSize: 13,
                          color: '#f87171',
                          cursor: 'pointer',
                          boxShadow: '0 1px 4px #f8717111',
                          zIndex: 2,
                          padding: 0,
                        }}
                      >
                        <span style={{fontWeight:700}}>×</span>
                      </button>
                    )}
                    <ReactMarkdown remarkPlugins={[remarkMath, remarkGfm]} rehypePlugins={[rehypeKatex]}>{summaryArr[para._summaryIndex] || ''}</ReactMarkdown>
                  </div>
                </div>
              );
            })}
            {paragraphs.length === 0 && <div style={{ color: '#888', fontSize: 16 }}>暂无内容</div>}
          </div>
        </>
      )}
      <JsonAnote show={showAnote} onClose={()=>setShowAnote(false)} onStart={handleStartAnote} />
    </div>
  );
};

export default BookReader;
